.user {
  margin: 0 20px 20px 0;
  display: flex;
  cursor: pointer;
  position: relative;
}

img {
  max-width: 80px;
  width: 100%;
}

.info {
  margin-left: 10px;
}

.info a {
  color: rgb(242, 121, 32);
}

.location {
  font-size: 13px;
}

.reputation {
  font-weight: 700;
}

.tag {
  text-align: center;
  font-size: 15px;
  color: rgb(194, 194, 194);
  text-decoration: none;
  margin: 5px 5px 0 0;
}

.about {
  margin-top: 10px;
}

.aboutUser {
  position: absolute;
  padding: 10px;
  background-color: rgb(80, 80, 80);
  color: rgb(194, 194, 194);
  top: -10px;
  left: -10px;
  display: none;
  z-index: 1;
  font-size: 10px;
}

.avatar:hover + .info .aboutUser{
  display: block;
  padding-left: 100px;
}

.avatar:hover img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}
