.header {
  height: 5vh;
  background-color: rgb(41, 41, 41);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px calc(100% - 90%);
  box-shadow: 0px 0px 10px 1px rgb(18, 18, 18);
}

.logo {
  display: flex;
  align-items: center;
}

.logoImg {
  max-width: 50px;
  width: 100%;
}

.logo span:nth-child(1) {
  font-weight: 800;
}

.menu {
  display: flex;
  align-items: center;
}

.menu li {
  list-style: none;
  padding: 0 20px;
  font-size: 22px;
  font-weight: 300;
  cursor: pointer;
  position: relative;
}

.menu li a {
  text-decoration: none;
  color: rgb(247, 247, 247);
}

.menu a::before {
  position: absolute;
  content: "";
  background-color: rgb(247, 247, 247);
  width: 0;
  height: 1px;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.3s;
}

.menu li a:hover:before {
  width: 70%;
}

.menu li a.active::before {
  width: 70%;
}


.wrapper {
  width: 30%;
  display: flex;
  margin-right: 20px;
}

#search {
  height: 65%;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid rgb(247, 247, 247);
  width: -webkit-fill-available;
  padding: 5px;
  margin-right: 5px;
  color: rgb(247, 247, 247);
}

.search,
.login {
  background-color: transparent;
  color: rgb(247, 247, 247);
  border: 1px solid rgb(247, 247, 247);
  padding: 5px;
  cursor: pointer;
  transition: 0.3s;
  cursor: pointer;
}

.search:hover {
  border-color: rgb(131, 131, 131);
}

.login {
  font-size: 18px;
  padding: 8px;
}

.login:hover {
  border-color: rgb(244, 122, 32);
  color: rgb(244, 122, 32);
}
