.item {
  box-shadow: 0 0 5px 1px rgba(24, 24, 24, 0.7);
  padding: 15px;
  margin-bottom: 10px;
  display: flex;
}

.rates {
  margin-right: 30px;
  text-align: center;
}

.desc {
  width: 100%;
  height: 100%;
  padding-top: 20px;
}

.text {
  font-size: 24px;
  color: rgb(244, 122, 32);
  text-decoration: none;
  position: relative;
}

.text::after {
  position: absolute;
  content: '';
  width: 0;
  height: 1px;
  background-color: rgb(244, 122, 32);
  left: 0;
  bottom: -1px;
}

.text:hover::after {
  width: 100%;
}

.ownersInfo {
  display: flex;
  margin-top: 30px;
  justify-content: flex-end;
  align-items: baseline;
}

.tags {
  margin: 15px 5px;
}

.answered p {
  margin-top: 10px;
}

.name, .reputation, .date {
  margin-right: 10px;
}

.tags p, .name p, .reputation p, .date p, .votes p, .answers p, .views p, .answered p {
  color: rgb(156, 156, 156);
  margin-bottom: 5px;
  font-size: 13px;
}

.tags div, .name div, .reputation div, .date div, .votes div, .answers div, .views div, .answered div {
  padding: 5px;
  text-align: center;
  background-color: rgb(80, 80, 80);
  border-radius: 5px;
  color: rgb(194, 194, 194);
  font-size: 15px;
}

.tags div {
  background-color: transparent;
  padding: 5px 0 0 0;
  text-align: left;
}

.tagsNames a {
  margin-right: 5px;
  padding: 5px;
  text-align: center;
  background-color: rgb(80, 80, 80);
  border-radius: 5px;
  text-decoration: none;
  color: rgb(194, 194, 194);
}

.name div a {
  text-decoration: none;
  color: rgb(194, 194, 194);
}
