
.sorting {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.sorting h2 {
  margin-right: 20px;
  font-weight: 500;
}

.sorting p {
  font-weight: 300;
  font-size: 20px;
  margin: 0 30px;
  cursor: pointer;
  position: relative;
}

.sorting p:after {
  position: absolute;
  content: '\2193';
  right: -20px;
  bottom: -1px;
  display: none;
}

.sorting p:before {
  position: absolute;
  content: '\2191';
  right: -20px;
  bottom: -1px;
  display: block;
}